

.dialogWrapper {
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease-out;

  &.dialogOn {
    visibility: visible;
    opacity: 1;
  }

  .mask {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    z-index: 9998;
    opacity: .618;
  }

  .dialog {
    position: fixed;
    width: 480px;
    left: 50%;
    top: 50%;
    box-sizing: border-box;
    padding: 0 25px;
    transform: translate(-50%, -50%);
    border-radius: 4px;
    background: rgba(255,255,255,1);
    box-shadow: 0px 1px 6px 1px rgba(0,0,0,0.16);
    overflow: hidden;
    z-index: 9999;
    opacity: 1;
  
    .dialogTitle {
      width: 100%;
      height: 60px;
      line-height: 60px;
      font-size: 20px;
      font-weight: bold;
      color: #948c8c;

      .closeIcon {
        cursor: pointer;
        float: right;
        width: 16px;
        height: 16px;
        margin-top: 18px;
        background: url(../../assets/dialog-chacha.png) no-repeat;
        background-size: 16px;
      }
    }
  
    .dialogContent {
      display: flex;
      align-items: center;
      color: #616161;
      width: 100%;
      line-height: 22px;
      font-size: 14px;
      box-sizing: border-box;
      padding: 20px 0 40px;
      border-bottom: 1px solid #e8e8e8;
    }
  
    .dialogOperator {
      width: 100%;
      overflow: hidden;
      margin: 18px 0;
  
      .dialogClose, .dialogConfirm {
        width: 90px;
        float: right;
        border: none;
        border-radius: 4px;
        outline: none;
        height: 35px;
        line-height: 35px;
        color: #898989;;
        cursor: pointer;
      }

      .dialogClose {
        background: #ffffff;
      }

      .dialogConfirm {
        color: #ffffff;
      }
  
      .dialogClose:active, .dialogConfirm:active {
        opacity: .6;
        transition: opacity .3s;
      }

      .danger {
        background: #F56453;
      }

      .normal {
        background: #00B548;
      }
    }
  }
}

.tipWrapper {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  z-index: 9999;
  bottom: 52px;
  text-align: center;
  font-size: 14px;
  color: #ffffff;
  transition: all .3s ease-out;
  width: 100%;

  &.tipOn {
    visibility: visible;
    opacity: 1;
    bottom: 62px;
  }

  label {
    display: inline-block;
    max-width: 290px;
    height: 36px;
    line-height: 36px;
    padding: 0px 20px;
    border-radius: 36px;
    background: #000;
    opacity: .85;
    box-shadow: rgba(0, 0, 0, .15) 0px 1px 3px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

@media screen and (max-width: 768px) {
  .dialogWrapper {
    .dialog {
      width: 300px;

      .closeIcon {
        display: none;
      }
    }
  }
}