@import '../../globalVariable';

.separator {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width:100%;
  margin-top: 20px;
  text-align: center;
  .line {
    flex:auto;
    height: 1px;
    background: #eee;
  }
  .text {
    flex-basis: 40px;
    font-size: $middleFontSize;
    color: #949C9D;
  }
}