@import '../../globalVariable';

$buttonHeight: 40px;
$iconSize: 20px;
$loadingSize: 23px;

.buttonWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  height: $buttonHeight;
  line-height: $buttonHeight;
  border-radius: 6px;
  cursor: pointer;
  border: 1px solid #1FAE52;
  background: $yxLightGreen;
  color: #fff;
  font-size:$middleFontSize;
  .buttonMain {
    text-align: center;
    letter-spacing: -0.11px;
    position: relative;
    .buttonIcon {
      position: absolute;
      left: 0;
      top: 50%;
      width: $iconSize;
      height: $iconSize;
      transform: translate(-($iconSize+10), -50%);
      @include hide;
    }
  }
  .loadingIcon {
    position: absolute;
    right: 5%;
    top: 50%;
    width: $loadingSize;
    height: $loadingSize;
    transform: translate(0, -50%);
    @include hide;
  }
}