@import '../../globalVariable';

$buttonHeight: 42px;
$iconSize: 20px;
$loadingSize: 20px;

.buttonWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: $buttonHeight;
  height: 100%;
  border-radius: 6px;
  cursor: pointer;
  border: 1px solid #1FAE52;
  background: $yxLightGreen;
  color: #fff;
  font-size: $bigFontSize;
  .buttonMain {
    display: flex;
    align-items: center;
    letter-spacing: -0.11px;
    position: relative;
    .buttonIcon {
      position: absolute;
      left: 0;
      top: 50%;
      width: $iconSize;
      height: $iconSize;
      transform: translate(-($iconSize + 10), -50%);
      @include hide;
    }
  }
  .loadingIcon {
    position: absolute;
    right: 10%;
    top: 50%;
    width: $loadingSize;
    height: $loadingSize;
    font-size: 0;
    transform: translate(0, -50%);
    @include hide;
  }
}

:global(.mobile) {
  .buttonWrapper {
    border-radius: 5px;
    border: none;
  }
}

:global(.lightbox) {
  .buttonWrapper {
    border-radius: 3px;
    min-height:32px;
  }
}
